
import Navigator from './../../components/Navigator/Navigator.web';


let TextInputState = {
    _currentFocus: null,
    currentFocus: function() {
        return this._currentFocus;
    },
    blur: function(node) {
        if (node === this._currentFocus && node !== null) {
            try {
                node.blur();
            } catch (e) {}
            this._currentFocus = null;
        }
    },
    _timeoutID:"",
    focus: function(node) {
        // 确保在Navigator动画完成之后执行
        let that=this;
        let fn=()=>{
            if (that._currentFocus !== node && node !== null) {
                try {
                    node.focus();
                    console.log('node.focus');
                } catch (e) {
                    console.log('node.focus failed',e);
                }
                that._currentFocus = node;
            }
        };
        if(Navigator.runAfterAnimation){
            Navigator.runAfterAnimation(fn);
        }else{
          clearTimeout(this._timeoutID);
          this._timeoutID = setTimeout(fn,560);
        }

    }
};

export default TextInputState;
